.section {
  height: 2000px;
  margin-left: 350px;
  border-left: 3px solid $color-dark-bg;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 6px;
    z-index: -1;
    background-color: $color-dark-bg;

    @media (max-width: $breakpoint-med) {
      display: none;
    }
  }

  @media (max-width: $breakpoint-med) {
    margin-left: 0;
    border-left: none;
  }

  @mixin sectionHeaderColor($color, $darken-factor) {
    border-color: $color;
    h2, h3, p {
      color: $color;
    }
    .section-header svg path {
      fill: $color;
    }
    a {
      color: $color;
      border-color: $color;
    }
    .section-header {
      background-color: darken($color, $darken-factor);
      .section-bgbox {
        background-color: darken($color, $darken-factor - 4%);
      }
    }
    .section-codetext-rect {
      background-color: $color;
    }
    .section-codetext-text p {
      color: $color;
    }
    .section-startpoint-highlight {
      background-color: $color;
    }
    .section-cheader-pointfill {
      fill: $color;
    }
  }

  &.section-aboutme {
    @include sectionHeaderColor($color-about, 58%);
  }
  &.section-skills {
    @include sectionHeaderColor($color-skills, 62%);
  }
  &.section-experience {
    @include sectionHeaderColor($color-experience, 55%);
  }
  &.section-projects {
    @include sectionHeaderColor($color-projects, 58%);
  }
  &.section-contact {
    @include sectionHeaderColor($color-contact, 48%);
  }



  .section-header {
    border-top: 6px solid $color-dark-bg;
    border-bottom: 6px solid $color-dark-bg;
    border-right: 6px solid $color-dark-bg;

    background-color: $color-dark-bg;

    position: relative;
    height: 400px;
    display: flex;
    align-items: flex-end;

    @media (max-width: $breakpoint-med) {
      border-left: 6px solid $color-dark-bg;
      align-items: center;
      height: auto;

      border-top-width: 60px;
      border-bottom-width: 60px;
    }

    .section-line {
      position: absolute;

      z-index: 90;
      
      &.section-topline {
        top: 0px;
        left: 0px;
        right: 0;
        height: 3px;
      }
      &.section-bottomline {
        bottom: 0px;
        left: 0px;
        right: 0;
        height: 3px;
      }
      &.section-rightline {
        top: 0;
        bottom: 0;
        right: 0;
        width: 3px;
      }
      &.section-leftline {
        top: 0;
        bottom: 0;
        left: 0;
        width: 3px;
        display: none;
      }
      @media (max-width: $breakpoint-med) {
        &.section-leftline {
          display: block;
        }
      }
      

      &.section-aboutme {
        background-color: $color-about;
      }
      &.section-skills {
        background-color: $color-skills;
      }
      &.section-experience {
        background-color: $color-experience;
      }
      &.section-projects {
        background-color: $color-projects;
      }
      &.section-contact {
        background-color: $color-contact;
      }
    }

    .section-bgboxes {
      position: absolute;
      top: 3px;
      bottom: 3px;
      right: 3px;
      left: 0px;
      overflow: hidden;

      .section-bgbox {
        position: absolute;
        z-index: 1;

        //left: 5px;
        //top: 98px;
        width: 300px;
        height: 300px;

        transform: translate(-50%, -50%);
        transition: all 3s ease-in-out;

        will-change: all;

        $bgbox-sizes: (50vw, 25vw, 10vw, 8vw, 5vw);

        &.bgbox-1 {
          width: nth($bgbox-sizes, 1);
          height: nth($bgbox-sizes, 1);
        }

        &.bgbox-2 {
          width: nth($bgbox-sizes, 2);
          height: nth($bgbox-sizes, 2);
        }

        &.bgbox-3 {
          width: nth($bgbox-sizes, 3);
          height: nth($bgbox-sizes, 3);
        }

        &.bgbox-4 {
          width: nth($bgbox-sizes, 4);
          height: nth($bgbox-sizes, 4);
        }

        &.bgbox-5 {
          width: nth($bgbox-sizes, 5);
          height: nth($bgbox-sizes, 5);
        }

      }
    }

    .section-codetext {
      position: absolute;
      bottom: 10px;
      right: 10px;

      display: flex;
      flex-direction: row;
      align-items: center;

      z-index: 97;

      .section-codetext-rect {
        width: 6px;
        height: 14px;
        margin-left: 10px;
      }
      .section-codetext-text p {
        margin-top: 2px;
        font-family: $font-heading;
        font-weight: 900;
        font-size: 8px;
        letter-spacing: 1px;
        margin-bottom: 0;
        text-transform: uppercase;
      }
    }

    .section-inner {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 10px;
      z-index: 95;

      @media (max-width: $breakpoint-med) {
        flex-direction: column;
        margin-bottom: 40px;
        width: 100%;

        margin-top: 20px;
      }

      .section-inner-image {
        height: 200px;
        width: 250px;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          transform: scale(2);
          path {
          }
        }
      }
      .section-inner-content {
        @media (max-width: $breakpoint-med) {
          width: 100%;
        }
        .section-content-title {
          h2 {
            text-transform: uppercase;
            font-size: 40px;
            margin-bottom: 5px;
            margin-top: 0;
            @media (max-width: $breakpoint-med) {
              text-align: center;
            }
          }
        }
        .section-content-description {
          p {
            font-size: 12px;
            color: white;
            margin: 0;
            max-width: 400px;
            @media (max-width: $breakpoint-med) {
              width: 90%;
              max-width: 400px;
              margin: 0 auto;
              text-align: center;
            }
          }
        }
        .section-content-links {
          margin-top: 30px;
          margin-bottom: 10px;

          ul {
            list-style: none;
            margin: 0;
            padding: 0;

            display: flex;
            flex-direction: row;

            @media (max-width: $breakpoint-med) {
              flex-direction: column;
              justify-content: center;

            }
            li {
              @media (max-width: $breakpoint-med) {
                width: 80%;
                max-width: 350px;
                margin: 0 auto;
              }
              a {
                font-family: $font-heading;
                font-weight: 700;
                display: block;
                border: 2px solid;
                padding: 8px 40px;
                margin: 0 5px;

                font-size: 12px;
                text-decoration: none;

                @media (max-width: $breakpoint-med) {
                  width: auto;
                  margin: 0;
                  margin-bottom: 10px;
                }
              }
            }
          }
        }
      }
    }
  }

  .section-content {
    display: flex;
    flex-direction: column;

    $section-content-linewidth: 4px;

    .section-content-startpoint {
      //width: 100%;
      display: flex;
      flex-direction: column;

      padding-left: 10%;
      margin-top: 80px;

        .section-startpoint-square {
          border: 4px solid black;
          height: 22px;
          width: 22px;

          position:relative;
          transform: translateX(-50%);
          .section-startpoint-highlight {
            height: 15px;
            width: 15px;

            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
          }
        }
        .section-startpoint-line {
          width: $section-content-linewidth;
          height: 40px;
          background: black;
          transform: translateX(-50%);
        }
    }

    .section-content-cheader {
      display: flex;
      flex-direction: row;
      position: relative;
      .section-cheader-lines {
        padding-left: 10%;
        position: relative;
        height: 260px;
        width: 0;
          .section-cheader-topline {
            background: black;
            position: absolute;

            top: -2px;
            bottom: calc(50% + 9px);
            width: $section-content-linewidth;
            transform: translateX(-50%);
            border-radius: 5px;
          }

          .section-cheader-bottomline {
            background: black;
            position: absolute;

            bottom: -2px;
            top: calc(50% + 9px);
            width: $section-content-linewidth;
            transform: translateX(-50%);
            border-radius: 5px;
          }

      }
      .section-cheader-angledlines {
        position: relative;
        .section-cheader-angledlinetop {
          position: absolute;
          top: calc(50% - 8px);
          left: -3px;

          width: 15px;
          height: $section-content-linewidth;
          background: black;

          transform: rotate(45deg);
          border-radius: 5px;
        }
        .section-cheader-angledlinebottom {
          position: absolute;
          bottom: calc(50% - 8px);
          left: -3px;

          width: 15px;
          height: $section-content-linewidth;
          background: black;

          transform: rotate(-45deg);
          border-radius: 5px;
        }
        .section-cheader-angledmiddleline {
          position: absolute;
          top: 50%;
          left: 8px;

          transform: translateY(-50%);
          width: 90px;
          height: $section-content-linewidth;

          border-radius: 5px;
          background: black;
        }
      }
      .section-cheader-point {
        position: relative;
        .section-cheader-pointwrap {
          position: absolute;
          top: 50%;
          left: 95px;
          transform: translateY(-50%);

          border-radius: 50%;
          width: 16px;
          height: 16px;
          border: 4px solid black;
          svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) scale(1.3);
          }
        }
      }
      .section-cheader-frames {
        position: relative;
        width: 100%;
        .section-cheader-frametop {
          position: absolute;
          left: 103px;
        }
        .section-cheader-framebottom {
          position: absolute;
          transform: rotate(180deg);

          top: 50%;
          right: 10%;
        }
        .section-cheader-frame-topline {
          position: absolute;
          left: 193px;
          right: 30%;
          top: 2px;

          height: 4px;
          background: black;
          border-radius: 5px;
        }
        .section-cheader-frame-bottomline {
          position: absolute;
          left: 30%;
          right: calc(10% + 91px);
          bottom: 7px;

          height: 4px;
          background: black;
          border-radius: 5px;
        }
      }
      .section-cheader-content {
        position: absolute;
        left: calc(10% + 200px);
        top: 50%;
        transform: translateY(-50%);
        right: 15%;
        h3 {
          letter-spacing: -1px;
          font-size: 26px;
        }
      }
    }


  }
}

.section-segment {
  width: 100%;
  .segment-inner {
    width: 80%;
    margin: 150px auto;
    max-width: 1100px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    position: relative;

    @media (max-width: $breakpoint-med) {
      flex-direction: column-reverse;
      width: 85%;
      margin: 70px auto;
    }

    .segment-inner-floaty {
      position: absolute;
    }

    .segment-inner-image-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
        img {
          width: 100%;
          height: 100%;
          max-width: 320px;
          max-height: 320px;

          margin: 0;
          padding: 3px;
          border: 4px solid black;
        }
      > span {
        font-family: $font-heading;
        text-transform: uppercase;
        font-weight: 900;
        margin-top: 6px;
        font-size: 12px;
      }
    }
    .segment-inner-content-container {
      margin-left: 70px;
      @media (max-width: $breakpoint-med) {
        margin-left: 0;
        margin-bottom: 20px;
      }
      h3 {
        font-size: 36px;
        margin-bottom: 20px;
        margin-top: 0;
      }
      p {
        color: black;
        font-size: 14px;
        max-width: 500px;
      }
    }
  }
}

.segment-hello {
  .segment-inner-floaty {
    position: absolute;
    border: 3px solid $color-about;
    z-index: -1;

    @media (max-width: $breakpoint-med) {
      display: none;
    }

    &.segment-inner-floaty-1 {
      width: 110px;
      height: 110px;

      top: -20px;
      right: -20px;

      &::after {
        content: '';
        position: absolute;
        bottom: -5px;
        left: -5px;
        background: white;
        top: 10px;
        right: 10px;
      }
    }

    &.segment-inner-floaty-2 {
      width: 30px;
      height: 30px;

      left: -150px;
      top: 80px;
    }

    &.segment-inner-floaty-3 {
      width: 10px;
      height: 10px;

      bottom: -15px;
      right: 30px;
    }
  }
}